.App {
	text-align: center;
}

.App-logo {
	height: 20vmin;
	pointer-events: none;
}

.App-header {
	background-color: #a5b4a5;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #a34b07;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}

	to {
		transform: rotate(360deg);
	}
}

h2,
p {
	color: #003300;
}
